import styles from './ModalParagraph.module.css';


const ModalParagraph = (props) =>{
    if(props.text.length === 0)
    {        
        return;
    } 
    return(
        <div className={styles['content-container']}>
            <div className={styles['content-text']}>    
                <div className={styles['paragraph-container']}>
                    <p style={{whiteSpace: "pre-line"}} className={`${styles['paragraph-content']}`}>
                        {props.text}</p>
                </div>
            </div>            
        </div>
    )
}
export default ModalParagraph;