import styles from './Descriptions.module.css';
// icons
import linkedinIcon from '../../Icons/linkedin.png';
import pdfIcon from '../../Icons/adobeacrobatreader.svg';
//component
import SingleDescription from './SingleDescription';

 const descriptions_array = [
    {
      id: 'd1',
      title: 'About me',
      content: `Unity developer for the past 3 years mostly for PC, passionate about videogames and stylized graphics in general. Loves to learn new things and solve problems.
      Most of my work is low-level prototype assets or self-made projects.`,
      url: '/files/CV-Rodrigo-Eng.pdf',
      icon_url: pdfIcon,
      linkcontent: 'Resume'
    },
    {
      id: 'd2',
      title: 'Contacts',
      content: `Email: rodrigoguzman1202@gmail.com 
      Phone: +56950503351
      Discord: Kaza#1880`,
      url: 'https://www.linkedin.com/in/rogguzman/',
      icon_url: linkedinIcon,
      linkcontent: 'LinkedIn'
    }  
  ];

const Descriptions = () =>{
    return(
    <div className={styles['desc-container']} >  
        {descriptions_array.map((descriptionsMap) =>
            <SingleDescription             
              key ={descriptionsMap.id}
              title={descriptionsMap.title}
              url={descriptionsMap.url}
              content={descriptionsMap.content}
              icon_url ={descriptionsMap.icon_url}
              linkcontent={descriptionsMap.linkcontent}/>              
            )}                           
      </div> 
    );
    
}
export default Descriptions;