import styles from './Experiences.module.css';

//images
import TT from  '../../img/TheTravelers.png';
import xmas from '../../img/xmas.png';
//component
import Experience from './Experience';
const experiences_array =[
    {
      id: 'e1',
      title: 'The Travelers',
      url_image: TT,
      descriptions: `Travelers: “Ex umbra in solem” is a 3D isometric narrative videogame with anime style where Tazani the holder of light and Zafiro the darkness envoy have to work together to save the world from
      spirits with negative emotions. `,
      url_read_more: ''
    },
    {
      id: 'e2',
      title: 'Christmas',
      url_image: xmas,
      descriptions: `Unity animated scene designed with a christmas theme. Mostly made with blender to create the 3D enviroment and toon shader for the style.`,
      url_read_more: ''
    },
   
  ];

const Experiences = () =>{
    return(        
        <div className={styles['experience-container']}>            
                  {experiences_array.map((experienceMap) =>                 
                    <Experience 
                        key = {experienceMap.id}
                        title = {experienceMap.title}
                        url_image = {experienceMap.url_image}
                        descriptions = {experienceMap.descriptions}
                        url_read_more = {experienceMap.url_read_more}                       
                    />
                  )}                  
            </div>   
    )
}
export default Experiences;