import styles from './Modal.module.css';
import ModalEntry from './ModalEntry';
import closeicon from '..//../Icons/closeicon.png';
import ModalHeader from './ModalHeader';
import {useSelector,useDispatch} from 'react-redux';
import { modalActions } from '../../store';

const Modal = (props) =>{        
    const dispatch = useDispatch();     
    const modalState = useSelector( state => state.modalState);
    const modalarray = useSelector(state => state.modalarray);
    const loading = useSelector (state => state.loading);
      
    const close = () =>{                
        dispatch(modalActions.resetModal());
        document.body.style.overflow ='unset';       
    }

    if (!modalState){
        return;        
    }     
    document.body.style.overflow = 'hidden';            
    return(
        <div className={styles['modal-body']}>
        <div onClick={close} className={styles.overlay} ></div>       
            <div  onClick={close} className={styles['close-button']} ><img alt='close-icon' className={styles['close-icon']} src={closeicon}/> </div>   
            <div className={styles['modal-container']}>
            
           
           { !loading && <ModalHeader
            key ={modalarray.id+"Headkey"} 
            modalDataTitle = {modalarray.id}
            modalDatavid = {modalarray.vid} 
            modalDataBanner = {modalarray.banner}
            />   }                             
            {!loading && modalarray.paragraphs.map((paragraphMap) =>              
                    <ModalEntry 
                    key = {paragraphMap.id} 
                    title = {paragraphMap.title}
                    section_title = {paragraphMap.section_title}
                    paragraph = {paragraphMap.description}
                    media_url = {paragraphMap.media_url}
                    vid = {paragraphMap.vid}
                    downloadable = {paragraphMap.downloadable}
                    media_comment = {paragraphMap.media_comment}
                    media_comment_url = {paragraphMap.media_comment_url}
                    extra = {paragraphMap.paragraph}
                    extra_img = {paragraphMap.paragraph_media}
                    />                         
            )
            }       
                         
            </div>
        </div>       
    );
} 

export default Modal;