import styles from './Skillsingle.module.css';

const Skillsingle = (props) =>{
    return(
    <div className={styles['content-container']}>
         <img className={styles['icon']} src={props.url_icon} alt=""/> 
        <p className={styles['content-text']}> {props.skill_name}  </p>
    </div>
    )
};
export default Skillsingle;