import styles from './SingleDescription.module.css';

const SingleDescription = (props) =>{
    return(
        <div className={styles.container}>
            <div className={styles['title-container']}>
                <h4 className={styles.title}>{props.title}</h4>
            </div>
            <div className={styles['content-container']}>
                <p>{props.content}</p>
            </div>
            <div className={styles['link-container']}>
                <div className={styles.row}>
                    <img className={styles.img} alt='icon' src={props.icon_url}/>
                        <p className={styles.p}><a className={styles.hyperlink + [' hyperlink--cta']} href={props.url} download>{props.linkcontent} </a></p>
                    </div>
                <div className={styles.row}>
                    <p className={styles.p}><a className={styles.hyperlink + [' hyperlink--cta']} href={props.url_2}>{props.linkcontent_2} </a></p>
                </div>
            </div>
        </div>
   );
};

export default SingleDescription;