import styles from './History.module.css'
import Historial from './Historial'

import rem_portait from '../../img/Rem2.png'
import headworld from '../../img/Headworld.png'
import kelowar from '../../img/Kelo.png'
import pyroboom from '../../img/PyroBoom2.png'
import unity_projects from '../../img/Projects.png'
import xmas from '../../img/xmas.png'
import tTlogo from '../../img/logosoloLETRAScolor (1).png'

const History = (props) =>{
    
    const history_array = [
        {
          id: 'hminus1',
          date: '2023',
          title: 'Portfolio',
          ready: false,
          charge: 'ReactJS developer',
          description: `This portfolio was recently made with ReactJS which i recently learned thus is part of my portfolio :)`,
          url_read_more: '',
          url_image : ''
        },
        {
          id: 'h0',
          date: '2022',
          title: 'Christmas',
          ready: true,
          charge: '3D modeler- VFX artist - Shader artist',
          description: `Developed a solo scene with christmas in mind, 
          creating 3D models and modifying Unity chan toon shader to add snow to a stylized enviroment`,
          url_read_more: '',
          url_image : xmas
        },
        {
          id: 'h1',
          date: '2022',
          title: 'The Travelers',
          ready: true,
          charge: 'Technical Artist',
          description: `Most of my duties included creating UI with navigation system (Gamepad), 
          shaders,VFX,optimization and established a pipeline.`,
          url_read_more: '',
          url_image : tTlogo
        },
        {
          id: 'h2',
          date: '2021',
          title: 'Rem from RE:Zero',
          ready: true,
          charge: '3D modeler - Texture artist - Rigger - Shader Artist',
          description: `I wanted to learn how to get stylized characters on the game engine , 
          i actually made a few more before this one but they're not that good. 
          I made this model using poly modelling following good reference and with the help of a Nhij Quang timelapse 
          that he has on his youtube channel.`,
          url_read_more: '',
          url_image : rem_portait
        },
        {
          id: 'h3',
          date: '2021',
          title: 'Headworld',
          ready: true,
          charge: 'Unity developer',
          description: `Technical prototype with dedicated server and client arquitecture. 
          Most of my duties were managing the client side be it handling network packages to match animations, 
          enviromental design, client side performance, day and night system, lightmaps and UI Elements.`,
          url_read_more: '',
          url_image : headworld
        },
        {
          id: 'h4',
          date: '2021',
          title: 'KeloWar',
          ready: false,
          charge: 'Unity developer',
          description: `Mobile development trying to replicate "Archero" mobile game mechanics, 
          made in only a few days. Really Simple with a character controller and AI for monsters.`,
          url_read_more: '',
          url_image : kelowar
        },
        {
          id: 'h5',
          date: '2020',
          title: 'Pyroboom',
          ready: false,
          charge: 'Unity developer',
          description: `First actual game made in a gamejam of 48 hours, Sadly its on the missed deadline because we didnt click "Send" on the jam website.`,
          url_read_more: '',
          url_image : pyroboom
        },
        {
          id: 'h6',
          date: '2019',
          title: 'Unity Tutorial projects',
          ready: false,
          charge: '--',
          description: `Most of these projects were made following any kind of tutorial in internet, be it from a course or youtube videos.`,
          url_read_more: '',
          url_image : unity_projects
        }
        
    ];
  
    return(
    <div className={styles['history-wrapper']}>     
      <ul className={styles['history-container']}>
          {history_array.map((historyMap) =>
          <Historial 
              key = {historyMap.id}
              date = {historyMap.date}
              title = {historyMap.title}
              charge = {historyMap.charge}
              description = {historyMap.description}
              url_read_more = {historyMap.url_read_more}
              url_image = {historyMap.url_image}
              ready = {historyMap.ready}      
          />
        )}            
    </ul>
  </div>);
};

export default History;