import styles from './ModalHeader.module.css';

const ModalHeader = (props) =>{
    return(
         <span className={styles['inherit']}>
            <div className={styles['title-container']}><h2>{props.modalDataTitle}</h2></div>  
            {props.modalDataBanner.length === 0 ? null :                  
            props.modalDatavid === true ?  
                <video className={styles['image-portrait']} controls ><source src={props.modalDataBanner} type="video/mp4"></source></video>
                : 
                <img className={styles['image-portrait']} src={props.modalDataBanner} alt=''/>  
            } 
         </span>
    );
}
export default ModalHeader;