import styles from './Footer.module.css';
import githubIcon from '../../Icons/github.png'
import linkedinIcon from '../../Icons/linkedin.png'

const Footer = () =>{
    return(<div className={styles['footer-container']}>
        <div className={styles['left']}>
            <ul className={styles.ul}>
                <li className={styles.li}><a className={styles.a} href="https://github.com/lKaza" >
                    <span> <img alt='git' className={styles.icon} src={githubIcon}/></span></a></li>
                <li className={styles.li}><a className={styles.a} href="https://www.linkedin.com/in/rodrigo-guzman-a5277115b/" >
                    <span><img alt='linkedin' className={styles.icon} src={linkedinIcon}/></span></a></li>
            </ul>
        </div>

        <div className={styles['right']}><p>
        © Rodrigo Guzmán - 2022
        </p>
        </div>
    </div>);
} ;
export default Footer;