import styles from './Historial.module.css';
import {useDispatch} from 'react-redux';
import { modalActions } from '../../store/index';

const Historial = (props) =>{
      
    const dispatch = useDispatch();    
    const setOpenModalHandler = () =>{
        dispatch(modalActions.getArrayData(props.title)); 
        dispatch(modalActions.toggleModal());
    }
    return(
        <li className={styles['history-container']}>
            <div className={styles['date-container']}>
                <p className={styles['date-text']}>{props.date}</p>
            </div>
        <div className={styles['details-container']}>
            <div className={styles['details-content']}>
                   { props.url_image.length === 0 ? 
                   <div className={styles['details-text-container-no-image']}>                    
                        <div className={styles['details-text-wrapper']}>
                            <div className={styles['title-segment']}> 
                                <span><h5 className={styles['details-text-title']}>{props.title} </h5></span>
                            </div>
                            <div className={styles['charge-segment']}> 
                                <span><p className={styles['details-text-charge']}> {props.charge}</p> </span>
                            </div>
                            <div className={styles['desc-segment']}> 
                                <p className={styles['details-text-desc']} >{props.description}</p>
                            </div>
                            <div className={styles['read-more-segment']}> 
                                <span className={styles['details-text-desc']}>                                   
                                    {props.ready === false ? 
                                    <p className={styles['hyperlink-not-ready']} href ={props.url_read_more}> 
                                    WIP
                                    </p> : 
                                    <p className={styles['hyperlink-not-ready']} href ={props.url_read_more}> 
                                    <button className={styles['button']} onClick={setOpenModalHandler} > Read more</button>
                                    </p>}
                                    
                                </span>
                            </div>
                        </div>
                    </div> 
                    :
                    <div className={styles['details-text-container']}>                    
                        <div className={styles['details-text-wrapper']}>
                            <div className={styles['title-segment']}> 
                                <span><h5 className={styles['details-text-title']}>{props.title} </h5></span>
                            </div>
                            <div className={styles['charge-segment']}> 
                                <span><p className={styles['details-text-charge']}> {props.charge}</p> </span>
                            </div>
                            <div className={styles['desc-segment']}> 
                                <p className={styles['details-text-desc']} >{props.description}</p>
                            </div>
                            <div className={styles['read-more-segment']}> 
                                <span className={styles['details-text-desc']}>
                                    
                                {props.ready === false ? 
                                <p className={styles['hyperlink-not-ready']} href ={props.url_read_more}> 
                                WIP
                                </p>
                                : 
                                    <p className={styles['hyperlink-not-ready']} href ={props.url_read_more}> 
                                    <button className={styles['button']} onClick={setOpenModalHandler} > Read more</button>
                                    </p>}
                                </span>
                            </div>
                        </div>
                    </div>
                    }
               {props.url_image.length === 0 ? null : 
               <div className={styles['image-container']}>
               {props.ready === false ? <img className={styles.center} alt={props.alt} src={props.url_image}/>
               
               : 
               <button className={` ${styles['button-image']} `} onClick={setOpenModalHandler} ><img className={styles['center-button']} alt={props.alt} src={props.url_image}/> </button> }                               
                </div>
                }
            </div>
        </div>
        
    </li>
    );

}

export default Historial;