import React from 'react';
import Zoom from 'react-medium-image-zoom';
import './ZoomStyles.css';
import styles from './ModalZoomImg.module.css';

const ModalZoomImg = (props) =>{
    return(
        <Zoom>
            <img className={styles['imgOrvid']}   
            src = {props.source}
            alt = "bruh"
            />
        </Zoom>
    )
}

export default ModalZoomImg;