const initialState = { modalState: false, title: '', modalarray: [], loading: false};
const modalSlice = createSlice({
    name: 'modal',
    initialState: initialState,
    reducers: {
        toggleModal(state) {            
            state.modalState = !state.modalState;
            state.loading = false;    
        },
        getArrayData(state,action){
            state.title = action.payload;
            const find_array = prelodaded_modals.find(x => x.id === state.title); 
            state.modalarray = find_array;
        },
        loading(state){
            state.loading = true;
        }, 
        resetModal(state){
            state.title = '';
            state.modalarray = [];
            state.modalState = !state.modalState;
        }          
    }
})

const store = configureStore({
    reducer: modalSlice.reducer
});

export const modalActions = modalSlice.actions;
export default store;