import styles from './Skills.module.css';

import blenderIcon from '../../Icons/blender.svg';
import unityIcon from '../../Icons/unity.svg';
import csharpIcon from '../../Icons/csharp.svg';
import typescriptIcon from '../../Icons/typescript.svg';
import pythonIcon from '../../Icons/python.svg';
import githubIcon from '../../Icons/github.svg';
import react from '../../Icons/react.svg'
import sql from '../../Icons/mysql.svg'
import jira from '../../Icons/jira.svg'
import slack from '../../Icons/slack.svg'
import photoshop from '../../Icons/adobephotoshop.svg'
import hlsl from '../../Icons/hlsl.png'
import trello from '../../Icons/trello.svg'

import Skillrow from './Skillrow';


const Skills = (props) =>{
    const skills_array =[
        {id: 's1',
          title: 'Languages',
    
          skill_name_1:'C#',
          url_icon_1: csharpIcon,
    
          skill_name_2: 'Typescript',
          url_icon_2: typescriptIcon,
    
          skill_name_3:'Python',
          url_icon_3: pythonIcon,
    
          skill_name_4:'HLSL',
          url_icon_4: hlsl,
    
          skill_name_5: 'SQL',
          url_icon_5: sql,
    
          skill_name_6:'React',
          url_icon_6: react
        },
        {
          id: 's2',
          title: 'Engine',
    
          skill_name_1:'Unity',
          url_icon_1: unityIcon,
    
          skill_name_2: '',
          url_icon_2: '',
    
          skill_name_3:'',
          url_icon_3: '',
          
          skill_name_4:'',
          url_icon_4: '',
          
          skill_name_5: '',
          url_icon_5: '',
    
          skill_name_6:'',
          url_icon_6: ''
          
        },
        {
          id: 's3',
          title: 'Tools',
    
          skill_name_1:'Github',
          url_icon_1: githubIcon,
    
          skill_name_2: 'Blender',
          url_icon_2: blenderIcon,
    
          skill_name_3:'Photoshop',
          url_icon_3: photoshop,
    
          skill_name_4:'Jira',
          url_icon_4: jira,
    
          skill_name_5: 'Slack',
          url_icon_5: slack,
    
          skill_name_6:'Trello',
          url_icon_6: trello
        },
    
      ];

    return(
      <div className={styles['skill-container']}>      
        <div className={styles.skills}>                         
              {skills_array.map((skillMap) =>
                        <Skillrow 
                        key={skillMap.id}
                        title={skillMap.title}
                        skill_name_1={skillMap.skill_name_1}
                        url_icon_1={skillMap.url_icon_1}
                        skill_name_2={skillMap.skill_name_2}
                        url_icon_2={skillMap.url_icon_2}
                        skill_name_3={skillMap.skill_name_3}
                        url_icon_3={skillMap.url_icon_3}
                        skill_name_4={skillMap.skill_name_4}
                        url_icon_4={skillMap.url_icon_4}
                        skill_name_5={skillMap.skill_name_5}
                        url_icon_5={skillMap.url_icon_5}
                        skill_name_6={skillMap.skill_name_6}
                        url_icon_6={skillMap.url_icon_6}/>                                          
              )}                                                 
        </div>
    </div>);
};

export default Skills;