import styles from './Skillrow.module.css'
import Skillsingle from "./Skillsingle";

const Skillrow = (props) =>{
    return(<div className={styles.skills}> 
    <div className={styles.inline}>
        <h1 className={styles['content-title']}>{props.title}</h1>
            <Skillsingle url_icon={props.url_icon_1} skill_name ={props.skill_name_1}/>
            <Skillsingle url_icon={props.url_icon_2} skill_name ={props.skill_name_2}/>
            <Skillsingle url_icon={props.url_icon_3} skill_name ={props.skill_name_3}/>
            <Skillsingle url_icon={props.url_icon_4} skill_name ={props.skill_name_4}/>
            <Skillsingle url_icon={props.url_icon_5} skill_name ={props.skill_name_5}/>
            <Skillsingle url_icon={props.url_icon_6} skill_name ={props.skill_name_6}/>     
        </div>  
    </div>
    );

}

export default Skillrow;